export default {
	data() {
		return {
			menu:[
				{
					title:'平台介绍',
					img:'../../static/code.jpg',
					url:'/pages/platform/companyIntro/companyIntro'
				},{
					title:'平台商城',
					img:'../../static/logo.png',
					url:'/pages/platform/shop/shop'
				},{
					title:'平台需求',
					img:'../../static/logo1.png',
					url:'/pages/platform/need/need'
				},{
					title:'我要拜访',
					img:'../../static/logo.png',
					url:'/pages/platform/visit/visit'
				},
			],
			tab: [
				{
					name: '相关产品',
					id: 0,
				},
				{
					name: '相关案例',
					id: 1,
				},
				
			],
			tabid:0,
			showProduct:true,
			showCase:false,
			platform:'',
			product:[],
			cases:[],
		}
	},
	onLoad() {
		// this.dataReq();
		this.getPlatform();
	},
	onShow() {
		
	},
	methods: {
		changeTab(id){
			this.tabid = id;
			// console.log(id);
			if(this.tabid == 0){
				this.showProduct = true;
				this.showCase = false;
			}
			else if(this.tabid == 1){
				this.showProduct = false;
				this.showCase = true;
			}
		},
		
		//获取平台信息
		async getPlatform(){
			var res = await this.$request.get('/platform',);
			// console.log(res);
			this.platform = res.data.result[0];
			this.productQuery(this.platform.cProducts);
		},
		productQuery(data){
			console.log(data);
			for(var i = 0 ; i < data.length; i ++){
				if(data[i].uid == " " || data[i].uid == null){
					this.getPlatform();
				}else{
					if(data[i].type == 1){
						this.product.push(data[i]);
						// console.log(this.product);
					}else if(data[i].type == 0){
						this.cases.push(data[i]);
						// console.log(this.cases);
					}
				}
				
			}
		},
		productDetail(uid){
			console.log(uid);
			uni.navigateTo({
			      url: '/pages/detail/detail?uid=' + uid,
			})
		},
		
		goPlatform(url){
			console.log(url);
			uni.navigateTo({
				url:url
			})
		},
	},
}
